/* extra small -- container 550px
     small 768px -- container 750px;
     medium 992px -- container 970px;
     large 1200px -- container 1170px;  */
* {
  box-sizing: border-box;
}

.row {
  margin-bottom: 0;
}

.row:last-child .col:last-child {
  margin-bottom: 0;
}

.row:after {
  content: " ";
  display: table;
  clear: both;
}

.col,
.col-1,
.col-2,
.col-3,
.col-4,
.col-5,
.col-6,
.col-7,
.col-8,
.col-9,
.col-10,
.col-11,
.col-12 {
  position: relative;
  float: left;
  margin-bottom: 1.5em;
  box-sizing: border-box;
}

.col:before,
.col:after {
  content: " ";
  display: table;
  overflow: auto;
}

.col:after {
  clear: both;
}

.col {
  *zoom: 1;
}

.col figure {
  min-width: 100%;
  margin-left: 0%;
}

