body {
  background: #222;
}

.container {
  width: 1170px;
  min-height: 100vh;
  background: #fff;
  margin: 0 auto;
}

#load_img {
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.65);
  display: none;
  position: fixed;
  border-radius: 0px;
}
#load_img img {
  width: 80px;
  margin-top: 18%;
  opacity: 1;
}

.boletos {
  display: none;
  padding: 20px 60px;
  background: #fff;
  overflow: hidden;
  width: 100%;
  height: auto;
  margin: 0px 0px;
}
.boletos div {
  padding: 15px 15px;
  float: left;
  border: 1px solid #ccc;
  width: 14.28%;
  max-height: 90px;
}
.boletos div div {
  padding: 0px;
  width: 50%;
  height: auto;
  border: 0px;
}
.boletos div div label {
  float: left;
  width: 30%;
  background: #ddd;
  padding: 5px 0px 0px 6px;
}
.boletos div div input {
  float: right;
  width: 70%;
}
.boletos label {
  height: 27px;
  padding: 5px 5px;
  display: block;
  width: 100%;
  color: #333;
  font-size: 13px;
  margin-bottom: 10px;
  margin-top: -5px;
}
.boletos input {
  margin: 0px 0px;
  width: 100%;
  padding: 3px 0px 3px 5px;
  text-align: center;
}

.boletos:nth-child(even) {
  background: #f0f0f0;
}

.div-responsavel,
.div-aluno,
.div-curso {
  width: 33.3333% !important;
}
.div-responsavel label,
.div-aluno label,
.div-curso label {
  margin-top: 0px;
  width: 85px;
  float: left;
}
.div-responsavel input,
.div-aluno input,
.div-curso input {
  width: 231px !important;
  float: right !important;
  text-align: left;
}

.tag_boletos {
  background: #666 !important;
  width: 100% !important;
  height: 35px !important;
  padding: 10px !important;
  overflow: hidden;
  border-radius: 5px 5px 0px 0px;
  border: 0px !important;
}
.tag_boletos h5 {
  text-align: right;
  color: #fff !important;
  margin: 0 !important;
}

/*----------------Master----------*/
#boleto_master {
  border-top: 5px solid #444;
  display: block;
  margin-top: 0px;
  background: #f0f0f0;
}
#boleto_master #bg_blocks_master {
  border: 1px solid #aaa;
  overflow: hidden;
}
#boleto_master div {
  border-color: #aaa;
  width: 13.85%;
}
#boleto_master div label {
  color: #fff;
  background: transparent;
  color: #333;
}
#boleto_master .div-n-lanc {
  width: 20% !important;
}
#boleto_master .div-n-lanc div {
  width: 50%;
}
#boleto_master .div-n-lanc div label {
  width: 20%;
  float: left;
}
#boleto_master .div-n-lanc div input {
  width: 70%;
}
#boleto_master .div-n-parcela {
  width: 9.6% !important;
}
#boleto_master .div-vencimento {
  width: 15% !important;
}
#boleto_master select {
  width: 200px;
  padding: 4px 5px;
}
#boleto_master select option:first-child {
  display: none;
}

.bg_buttons {
  background: #222;
  display: none;
  overflow: hidden;
  padding: 10px 30px;
  position: fixed;
  width: 1170px;
}
.bg_buttons #btn_download_pdf {
  display: block;
  clear: both;
  background: #223892;
  border: 0px;
  padding: 15px 20px;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  margin: 0 auto;
  margin-top: 20px;
  margin-right: 60px;
  margin-bottom: 20px;
  float: right;
}

#btn_download_pdf:hover {
  background: #034ab6;
}

#bg_btn_gerar_boleto {
  width: 100% !important;
  padding: 20px;
  overflow: hidden;
}

#btn_gerar_boleto {
  display: block;
  background: #999;
  border: 0px;
  padding: 15px 20px;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  float: right;
  width: 150px;
}

#btn_gerar_boleto:hover {
  background: #1fa06e;
}

